export function sendRequest(setDataFn) {
  const host = 'https://api.football-data.org/'
  const newUrl = new URL(
    host + 'v2' + window.location.pathname + window.location.search
  )
  newUrl.searchParams.delete('search')
  console.log(
    '!fetch sendRequest url:',
    window.location.pathname + window.location.search
  )

  const xAuthToken = localStorage.getItem('token')
  // console.log('!fetch token:', xAuthToken)

  return fetch(newUrl.toString(), {
    headers: { 'X-Auth-Token': xAuthToken },
    dataType: 'json',
    type: 'GET',
  })
    .then((response) => response.json())
    .then((data) => {
      console.log('fetch data:', data)
      setDataFn(data)
    })
    .catch((err) => {
      // console.log(' !!! fetch ERROR:', err)
      setDataFn({ message: err.toString() })
    })
}
